input, textarea{
	background: transparent;
	padding: 5px;
	width: 60%;
	color:#a23d3d;
	margin-bottom: 15px;
	font-family: 'Raleway', sans-serif;
	font-size: 15px;
	border: none;
	border-bottom: 2px solid #a23d3d;
}
textarea{
	height: 40vh;
}
input::-webkit-input-placeholder, textarea::-webkit-input-placeholder{
	color: #a23d3d;
}
input:-ms-input-placeholder, textarea:-ms-input-placeholder{
	color: #a23d3d;
}
input::-ms-input-placeholder, textarea::-ms-input-placeholder{
	color: #a23d3d;
}
input::placeholder, textarea::placeholder{
	color: #a23d3d;
}
input[type="submit"]{
	width:100px;
	border: 2px solid #a23d3d;
	cursor: pointer;
}
input[type="submit"]:hover{
	background: #a23d3d;
	color: white;
}



