.post-list{
			display: grid;
			grid-template-columns: repeat(3, minmax(100px,293px));
			-ms-flex-pack: center;
			    justify-content: center;
			grid-gap: 25px;
		}
		.post{
			cursor: pointer;
			position: relative;
			display: block;
		}
		.post-image{
		margin:0;
		}
		.post-image img{
			width:100%;
			vertical-align: top;
		}
		.post-overlay{
			background: rgba(0, 0, 0, 0.5);
			position: absolute;
			left: 0;
			right: 0;
			bottom: 0;
			top: 0;
			display: none;
			-ms-flex-align: center;
			    align-items: center;
			-ms-flex-pack: center;
			    justify-content: center;
			color: white;
			text-align:center;
		}
		.post:hover .post-overlay{
			display: -ms-flexbox;
			display: flex;
			-webkit-transition: 1s;
			-o-transition: 1s;
			transition: 1s;
		}
		.post-likes,
		.post-comments{
			width: 80px;
			margin: 5px;
			font-weight: bold;
			text-align: center;
			display: inline-block;
		}
		@media screen and (max-width:768px){
			.post-list{
				grid-gap: 5px;
			}
}



